//---------------------------------------------fonts
$font_path: '../fonts/';
$font_1: 'Bebas', sans-serif;
$inter: 'Inter', sans-serif;
$raleway: 'Raleway', sans-serif;
$sfd: 'SFDistantGalaxy', sans-serif;
$sfdo: 'SFDistantGalaxyOutline', sans-serif;
$dmsans: 'DMSans', sans-serif;

$font_size_base: 1.6rem;
//---------------------------------------------fonts###

//---------------------------------------------colors
$white: #fff;
$black: #000;

$black_2: #141313;
$black_3: #070707;
$black_4: #222121;
$black_6: #040404;
$black_5: #212121;

$gray: #333333;

$yellow: #dce161;

$light_gray: #e7e7e7;

$color_text_base: $black;
$color_link_base: #4aa3df;
$body_bg: $white;
//---------------------------------------------colors###

//---------------------------------------------z-index
$z-index: (
	popup: 101,
	header: 100,
	footer: 100,
);
//---------------------------------------------z-index###

//---------------------------------------------layout
$page_width: 118.6rem;

//---------------------------------------------layout###

//---------------------------------------------global values
$gl_radius: 0.5rem;
//---------------------------------------------global values###

//---------------------------------------------media points
$point_1: 1200px;
$point_2: 1024px;
$point_3: 1023.5px;
$point_4: 768px;
$point_5: 640px;
$point_6: 480px;
$point_7: 375px;
$point_8: 320px;
$point_9: 390px;
//---------------------------------------------media points

//---------------------------------------------forms
$input_height: 3.6rem;
$input_offset: 0 0.8rem;
$input_font_size: $font_size_base;
$input_border_color: #ccc;
$input_border_color_active: $black;
$input_placeholder_color: rgba($black, 0.4);
$input_text_color: $black;
$input_radius: $gl_radius;
//---------------------------------------------forms###
