@import 'ScssHelpers/helpers-all.scss';

.blog_card {
	@include bp($point_2, min) {
		width: 47.7rem;
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 5.4rem;
		}

		@include bp($point_3) {
			margin-bottom: 5.15rem;
		}
	}
}

.blog_card--layout_big_mod {
	@include bp($point_2, min) {
		width: 70.1rem;
		margin-bottom: 6.6rem;
	}
}

.blog_card__link {
	display: grid;

	font-family: $raleway;
	font-size: 1.6rem;

	&:hover,
	&:focus {
		text-decoration: none;
		color: $black;
	}
}

.blog_card__picture {
	display: block;

	position: relative;

	@include bp($point_2, min) {
		width: 32.1rem;
		height: 16.8rem;
	}

	@include bp($point_3) {
		width: 32.6rem;
		height: 18.4rem;
	}

	&::before {
		content: '';
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		background-color: $yellow;
		z-index: -1;
	}

	.blog_card--layout_big_mod & {
		@include bp($point_2, min) {
			width: 64rem;
			height: 36.2rem;
		}
	}
}

.blog_card__img {
	@include cover_img;

	transition: opacity 0.3s ease;

	.blog_card__link:hover & {
		opacity: 0.64;
	}
}

.blog_card__content {
	position: relative;
	justify-self: flex-end;

	background-color: $light_gray;
	color: $black;

	z-index: 1;

	@include bp($point_2, min) {
		width: 31.4rem;
		min-height: 12rem;
		margin-top: -6.5rem;
		padding: 2rem 2rem 1.8rem 2rem;
	}

	@include bp($point_3) {
		width: 32.8rem;
		padding: 2rem 2rem 1.9rem 2rem;
		margin-top: -2.1rem;

		transform: translateX(1.2rem);
	}

	.blog_card--layout_big_mod & {
		@include bp($point_2, min) {
			width: 33.9rem;
			margin-top: -9.3rem;
		}
	}
}

.blog_card__title {
	@include title_uppercase;

	&:not(:last-child) {
		margin-bottom: 1.1rem;
	}
}

.blog_card__description {
	@include text_medium;

	padding-bottom: 1.1rem;
	border-bottom: 3px solid rgba($black, 0.6);
}
