@import 'ScssHelpers/helpers-all.scss';

.vertical_line {
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	width: 1.5rem;

	@include bp($point_2, min) {
		left: -8.9rem;
		bottom: 4.4rem;
	}

	@include bp($point_3) {
		left: -2.3rem;
	}
}

.vertical_line__line {
	width: 3px;
	height: 100%;
	margin-left: auto;
	margin-right: auto;

	background-color: rgba($white, 0.3);

	.vertical_line--dark_mod & {
		background-color: rgba($white, 0.3);
	}
}

.vertical_line__dot {
	position: absolute;
	width: 1.5rem;
	height: 1.5rem;

	border-radius: 100%;

	background-color: $white;

	&:before {
		content: '';
		position: absolute;
		width: 100%;
		height: 1rem;
		background-color: $black_2;
	}

	.vertical_line--dark_mod & {
		background-color: $white;

		&::before {
			background-color: $black_2;
		}
	}
}

.vertical_line__start {
	top: 0;

	&:before {
		top: 1.5rem;
	}
}

.vertical_line__end {
	bottom: 0;

	&:before {
		bottom: 1.5rem;
	}
}
