@mixin h1 {
	line-height: 1;
	font-weight: 400;

	@include bp($point_2, min) {
		font-size: 11.5rem;
	}

	@include bp($point_3) {
		font-size: 6.4rem;
	}
}

@mixin h2 {
	line-height: 1;
	font-weight: 400;

	@include bp($point_2, min) {
		font-size: 10rem;
	}

	@include bp($point_3) {
		font-size: 5.2rem;
	}
}

@mixin h3 {
	font-size: 9rem;
	font-weight: 400;
	text-decoration: underline;
}

@mixin h4 {
	line-height: 0.97;

	@include bp($point_2, min) {
		font-size: 4.5rem;
	}

	@include bp($point_3) {
		font-size: 4rem;
	}
}

@mixin h5 {
	font-size: 2.4rem;
}

@mixin h6 {
	font-size: 2rem;
}

@mixin text {
	font-size: 1.8rem;
}
// offsets

@mixin title_offset {
	margin-bottom: 2rem;
}

@mixin title_uppercase {
	font-weight: 600;
	font-size: 1.6rem;
	font-family: $raleway;
	text-transform: uppercase;
	font-style: italic;
}

@mixin text_medium {
	font-size: 1.6rem;
	line-height: 1.1;
	font-family: $raleway;
	font-weight: 500;
}
