@import 'ScssHelpers/helpers-all.scss';

.blog_list {
	@include bp($point_2, min) {
		padding-top: 13rem;
		padding-bottom: 13.3rem;
	}

	@include bp($point_3) {
		padding-top: 8.2rem;
		padding-bottom: 4.2rem;
	}
}

.blog_list__wrapper {
	@include bp($point_3) {
		padding-bottom: 7.4rem;
	}
}

.blog_list .section_in {
	position: relative;

	max-width: 113.8rem;
}

.blog_list__title {
	@include h1();

	color: $white;
	text-decoration: underline;

	@include bp($point_2, min) {
		margin-bottom: 8.5rem;
	}

	@include bp($point_3) {
		margin-bottom: 5.1rem;
		font-size: 7.1rem;
	}
}

.blog_list__top {
	display: flex;

	@include bp($point_3) {
		display: block;
		margin-bottom: 6.1rem;
	}
}

.blog_list__widget {
	@include bp($point_2, min) {
		width: 38.9rem;
	}
}

.blog_list__widget_title {
	@include h6;

	font-weight: 900;
	line-height: 1.15;
	text-transform: uppercase;
	font-family: $raleway;
	color: $white;
	font-style: italic;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 4.3rem;
		}
		@include bp($point_3) {
			margin-bottom: 3.8rem;
		}
	}
}

.blog_list__main_card {
	@include bp($point_2, min) {
		width: 70.1rem;
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-right: 4.8rem;
		}

		@include bp($point_3) {
			margin-bottom: 5.3rem;
		}
	}
}

.blog_list__wrapper {
	@include bp($point_2, min) {
		display: grid;
		grid-template-columns: 50% 50%;
	}
}

.blog_list__wrapper .blog_card:nth-child(3n + 2) {
	@include bp($point_2, min) {
		justify-self: flex-end;
	}
}

.blog_list__wrapper .blog_card:nth-child(3n) {
	@include bp($point_2, min) {
		grid-column: span 2;
		justify-self: center;
		margin-left: 11.6rem;
	}
}
