@import 'ScssHelpers/helpers-all.scss';

.widget_list {
	list-style: none;
	padding: 0;
	margin: 0;
}

.widget_list__item {
	width: 100%;
	display: block;

	border-bottom: 3px solid rgba($white, 0.3);

	@include bp($point_2, min) {
		padding-bottom: 1.7rem;
	}

	@include bp($point_3) {
		padding-bottom: 1.6rem;
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 1.9rem;
		}

		@include bp($point_3) {
			margin-bottom: 1.8rem;
		}
	}
}

.widget_list__link {
	display: block;

	color: $white;
	transition: color 0.3s ease;

	&:hover,
	&:focus {
		text-decoration: none;
		color: $yellow;
	}
}

.widget_list__link_title {
	@include title_uppercase;

	display: block;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 1rem;
		}
		@include bp($point_3) {
			margin-bottom: 1.2rem;
		}
	}
}

.widget_list__link_description p {
	@include text_medium;
}
